<template>
    <div id="app">
        <img src="./assets/logo_dark.png" alt="rent_logo" id="logoImg">
        <slide-verify ref="slideblock" @success="onSuccess" @again="onAgain" @fulfilled="onFulfilled" @fail="onFail"
            @refresh="onRefresh" :slider-text="text" :imgs="imgs" :accuracy="accuracy"></slide-verify>
        <div class="msg">{{ msg }}</div>
    </div>
</template>

<script>
import img0 from './assets/img.jpg';
import img1 from './assets/img1.jpg';
import img2 from './assets/img2.jpg';
import img3 from './assets/img3.jpg';
import img4 from './assets/img4.jpg';
import img5 from './assets/img5.jpg';
export default {
    beforeCreate: function () {
        document.getElementsByTagName('body')[0].className = 'body-bg'
    },
    name: 'App',
    data() {
        return {
            msg: '',
            text: 'Slide right->',
            imgs: [
                img0,
                img1,
                img2,
                img3,
                img4,
                img5,
            ],
            accuracy: 2, // 精确度小，可允许的误差范围小；为1时，则表示滑块要与凹槽完全重叠，才能验证成功。默认值为5
        }
    },
    methods: {
        onSuccess(times) {
            console.log('Verification succeeded');
            this.msg = `Verify success, spent ${(times / 1000).toFixed(1)}s`;
        },
        onFail() {
            console.log('Verification failed');
            this.msg = ''
        },
        onRefresh() {
            console.log('refresh');
            this.msg = ''
        },
        onFulfilled() {
            console.log('refereshed');
        },
        onAgain() {
            console.log('Robot detected.');
            this.msg = 'try again';
            // 刷新
            this.handleClick();
        },
        handleClick() {
            this.$refs.slideblock.reset();
            this.msg = ''
        },
    }
}
</script>

<style>
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    box-sizing: border-box;
    padding: 20px;
    color: #2c3e50;
    margin-top: 60px;
}

#logoImg {
    width: 200px;
    margin: 0 auto;
    position: relative;
    top: -30px;
    left: 50px;
}

.body-bg {
    background-color: #202531;
}

.msg {
    width: 300px;
    color: white;
    margin-top: 15px;
    text-align: center;
    font-weight: bold;
}
</style>



